export default {
	data() {
		return {
			activity_id: 0,
			
			searchText: '',
			searchResult: '',
			memberInfo: {},
			isFocus: false,
			showInfo: false,
			showRedTip: false,
			showCancelButton: false,
		}
	},
	onLoad: function(e) {
		this.activity_id = e.id;
		console.log(e)
	},
	methods: {
		searchStart: function() {	// 触发搜索
			if (this.searchText == '') {
				uni.showToast({
					title: '请输入微信号',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			console.log(this.searchText)
			
			let paras = {
				activity_id: this.activity_id,
				wechat: this.searchText
			}
			this.$http.request({
				url: '/activity/searchMember',
				method: 'post',
				params: paras
			}).then(res => {
				console.log(res)
				if (res.data.success) {
					this.showInfo = true
					if (parseInt(res.data.data.status) === 1) {
						this.showCancelButton = true
						this.showRedTip = false
					} else {
						this.showCancelButton = false
						this.showRedTip = true
					}
					this.searchResult = '以下是查询到的报名信息：';
					this.memberInfo = res.data.data;
							  
				} else {
					this.searchResult = res.data.error
					this.showInfo = false
					this.showCancelButton = false
					// this.searchResult = ''
					uni.showToast({
						title: '没有查到该微信号的报名信息',
						icon: 'none',
						duration: 1000
					});
				}
			}).catch(err => {
				console.log(err)
			})
				
		},
		startRecognize: function() {	//语音输入
			let _this = this;
			let options = {};
			options.engine = 'iFly';
			options.punctuation = false; // 是否需要标点符号 
			options.timeout = 10 * 1000;
			plus.speech.startRecognize(options, function(s) {
				_this.searchText = _this.searchText + s;
			});
		},
		
		cancelClick: function() {
			const _this = this;
			if (this.searchText == '') {
				uni.showToast({
					title: '请输入微信号',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			
			uni.showModal({
			    title: '有琴提示',
			    content: '确定要取消本次活动的报名么',
			    success: function (res) {
			        if (res.confirm) {
			            let paras = {
						  activity_id: _this.activity_id,
						  wechat: _this.searchText
						}
						_this.$http.request({
							url: '/activity/cancelActivity',
							method: 'post',
							params: paras
						}).then(res => {
							console.log(res)
							if (res.data.success) {
								uni.showToast({
									title: '取消成功'
								});
								setTimeout(function() {
									uni.navigateBack();
								}, 800);
							} else {
								uni.showToast({
									title: '取消失败，请重试',
									duration: 1000
								});
							}
						}).catch(err => {
							console.log(err)
						})
			        }
			    }
			});
			
		}
	}
}
